import React from 'react';
import Dialog from './Dialog';
import Button from './Button';

export default function SubmitScoreDialog({ open, onOpenChange, score, scoreLabel = '', onSubmit, onClose, user }) {

  const handleSubmit = () => {
    onSubmit();
    onOpenChange(false);
  };

  const handleClose = () => {
    if (onClose) onClose();
    onOpenChange(false);
  };

  return (
    <Dialog open={open} onOpenChange={handleClose} title=" ">
      <div className="text-center">
        <p className="text-white/80 mb-4"> : {score}</p>
        <p className="text-white/80 mb-4">  ?</p>
        <div className="flex justify-center gap-4">
          <Button onClick={handleSubmit}></Button>
          <Button variant="secondary" onClick={handleClose}></Button>
        </div>
      </div>
    </Dialog>
  );
}