import React from 'react';
import { motion } from 'framer-motion';
import '../crt-styles.css';
import config from './config'; // duration(ms) 

/**
 *   ( )
 * -    1    &   
 * -     ()  
 */
const CRTon = () => {
  const { duration } = config;         // ms
  const d = duration / 1000;           // sec
  const HOLD_BLACK = 1.0;              //      0.5

  // / : 0.5  
  const lineDelay = HOLD_BLACK;
  const lineDur   = d * 0.45;
  const dotDelay  = HOLD_BLACK;
  const dotDur    = d * 0.35;

  return (
    <motion.div className="crt-on-effect" initial={{ opacity: 1 }} animate={{ opacity: 1 }} transition={{ duration: 3, delay: d }}>
      {/* A) :   (0.5 )         */}
      <motion.div
        className="crt-blackout"
        initial={{ opacity: 1 }}
        animate={{ opacity: 0 }}
        transition={{
          delay: HOLD_BLACK,            //  0.5   
          duration: 0.12,               //  
          ease: 'easeIn'
        }}
      />

      {/* B) ( ): 0.5       */}
      <motion.div
        className="crt-bright-dot"
        initial={{ scale: 0, opacity: 0 }}
        animate={{
          scale:   [0, 1.8, 0.18, 0],
          opacity: [0, 1, 0.5, 0],
        }}
        transition={{
          delay: dotDelay,
          duration: dotDur,
          times: [0, 0.4, 0.75, 1],
          ease: 'easeInOut',
        }}
      />

      {/* C) : 0.5     */}
      <motion.div
        className="crt-h-line"
        initial={{ opacity: 0, scaleX: 0 }}
        animate={{
          opacity: [0, 1, 1, 0.9, 0],
          scaleX:  [0, 0.28, 1, 1, 1],
        }}
        transition={{
          delay: lineDelay,
          duration: lineDur,
          times: [0, 0.25, 0.65, 0.9, 1],
          ease: 'easeInOut',
        }}
      />
    </motion.div>
  );
};

export default CRTon;
