import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';

const colors = ["#FFC300", "#FF5733", "#C70039", "#900C3F", "#581845", "#33FF57", "#3357FF"];

const FireworkParticle = ({ x, y, size, color, delay }) => {
    const destinationX = x + (Math.random() - 0.5) * 400; //   
    const destinationY = y + (Math.random() - 0.5) * 400; //   

    return (
        <motion.div
            className="absolute rounded-full"
            style={{ x, y, width: size, height: size, backgroundColor: color, boxShadow: `0 0 15px ${color}` }} //  
            initial={{ opacity: 1, scale: 0 }}
            animate={{ opacity: [1, 1, 0], scale: [0, 1.5, 0], x: destinationX, y: destinationY }} //   
            transition={{ duration: 1.8, delay, ease: "easeOut" }} //   
        />
    );
};

const FireworkBurst = ({ x, y, count = 70, burstDelay }) => { //   
    const particles = Array.from({ length: count }).map((_, i) => ({
        id: i,
        size: Math.random() * 5 + 2, //    
        color: colors[Math.floor(Math.random() * colors.length)],
        delay: burstDelay + Math.random() * 0.3, //    
    }));

    return <>{particles.map(p => <FireworkParticle key={p.id} x={x} y={y} {...p} />)}</>;
};

const Fireworks = ({ duration = 4000 }) => {
    const [bursts, setBursts] = useState([]);
    const totalBursts = 5; //   

    // [] useEffect  
    useEffect(() => {
        //      
        const newBursts = Array.from({ length: totalBursts }).map((_, index) => {
            const burstX = Math.random() * window.innerWidth;
            const burstY = Math.random() * (window.innerHeight * 0.7) + (window.innerHeight * 0.1);

            return {
                // Date.now()  index     id 
                id: `firework-burst-${index}`, 
                x: burstX,
                y: burstY,
                burstDelay: index * (duration / 1000 / totalBursts),
            };
        });
        
        //       
        setBursts(newBursts);

    }, [duration]);

    return (
        <div className="fixed inset-0 pointer-events-none z-[101] overflow-hidden">
            {bursts.map(firework => (
                <FireworkBurst
                    key={firework.id}
                    x={firework.x}
                    y={firework.y}
                    burstDelay={firework.burstDelay}
                />
            ))}
        </div>
    );
};

export default Fireworks;