import React, { useEffect } from 'react';
import config from './config.js';
import './style.css'; //  !

export default function Wiggle({ duration }) {
  const effectConfig = { ...config, duration };

  useEffect(() => {
    const target =
      document.querySelector('.arcade-machine') ||
      document.getElementById('root') ||
      document.documentElement;

    const { rootClassName, duration } = effectConfig;

    if (!target) return;

    // () CSS  
    target.style.setProperty('--wiggle-duration', `${(duration || 800)}ms`);
    target.classList.add(rootClassName);

    return () => {
      target.classList.remove(rootClassName);
      target.style.removeProperty('--wiggle-duration');
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []); //  

  return null;
}
